@use '0-settings/variables' as var;

/**
 * Shared declarations for certain elements.
 */

h1, h2, h3, h4, h5, h6,
ul, ol, dl,
blockquote, p, address,
hr,
table,
fieldset, figure,
pre {
  margin-bottom: 30px;
}


/**
 * Consistent indentation for lists.
 */

ul, ol,
dd {
  margin-left: var.$base-spacing-unit;
}
