@use '0-settings/variables' as var;
/* Contact Links */
.contact {
  text-align: center;
  .contact__item {
    display: inline-block;
    margin: 0 3px;
  }

  .contact__link {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    font-size: 16px;
    line-height: 1;
    border-radius: 50%;
    color: var.$white;
    background-color: var.$dark;
    &:hover {
      color: var.$dark;
      background-color: var.$brand-color;
    }
  }
}
